* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin: 0 auto;
}
.card {
  margin-top: 10px;
}
.loading {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border-bottom: 3px solid dodgerblue;
  animation: logo infinite 1s;
  margin: 0 auto;
}
@keyframes logo {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.parent {
  display: flex;
}
.parent .nav {
  display: flex;
  margin-left: auto;
}
ul,
li {
  list-style: none;
}
a {
  text-decoration: none;
}
.nav ul {
  display: flex;
  margin: 10px;
  justify-content: space-between;
}
ul li a {
  color: white;
  padding: 10px;
}
.repo a {
  color: crimson;
}
.repo {
  margin: 10px;
  padding: 10px;
}
